import React, { useState } from "react";
import axios from "axios";
import "./index.css";
import schooLogoPng from '../../images/school_logo.png'
import { useNavigate } from 'react-router-dom';

// interface UserData {
//   username: string;
//   password: string;
// }

export const Login = React.memo(() => {
  const [userId, setUserId] = useState("");
  const [password, setPassword] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const navigate = useNavigate();

  const handleUserIdChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setUserId(e.target.value);
  };

  const handlePasswordChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setPassword(e.target.value);
  };

  const handleLogin = async () => {
    try {
      const response = await axios.post(
        "http://localhost:8000/login", 
        { username: userId, password },
        { withCredentials: true }
      );
      if (response.status === 200) {
        console.log('')
        // 
        // 
        navigate('/system');
      }
    } catch (error: any) {
      if (error.response && error.response.status === 401) {
        setErrorMessage("*");
      } else {
        console.error(":", error);
        setErrorMessage("*");
      }
    }
  };

  return (
    <div className="login-screen">
      <div className="login-screen-box">
        <div className="login-screen-box-header">
          <img className="login-screen-box-header-logo" alt="school-logo" src={schooLogoPng} />
          <div className="login-screen-box-header-title"></div>
          <div className="login-screen-box-header-student"></div>
        </div>
        <div className="login-screen-box-1">
          <div className="login-screen-box-1-title">ID/</div>
          <input
            className="login-screen-box-1-input"
            type="text"
            value={userId}
            onChange={handleUserIdChange}
            placeholder="ID"
          />
        </div>
        <div className="login-screen-box-2">
          <div className="login-screen-box-2-title"></div>
          <input
            className="login-screen-box-2-input"
            type="password"
            value={password}
            onChange={handlePasswordChange}
            placeholder=""
          />
        </div>
        {errorMessage && <div className="login-screen-box-3">{errorMessage}</div>}
        <div className="login-screen-box-4" onClick={handleLogin}>
            <div className="login-screen-box-4-title"></div>
        </div>
      </div>
    </div>
  );
});
